import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IOptionsPsContainerPanel } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsComplexFacilityRequestExposed } from './ps-complex-facility-request.component.interfaces';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { PsDropdownProductTypesComponent } from '../../ps-select-dropdown/ps-dropdown-product-types/ps-dropdown-product-types.component';


/**
 * @author Heba.Hassan
 * @since 20/04/2020
 * 
 * <p> PsComplexFacilityRequestComponent is a complex component composed for facility request</p>
 */
@Component({
  selector: 'ps-complex-facility-request',
  templateUrl: './ps-complex-facility-request.component.html',
  styleUrls: ['./ps-complex-facility-request.component.scss'],
})
export class PsComplexFacilityRequestComponent extends PsBaseFieldComponent implements OnInit {

  @Input() options: IOptionsPsComplexFacilityRequestExposed;
  @Output() public facilityTypeChange: EventEmitter<any> = new EventEmitter<any>();
  @Output() public productClassChange: EventEmitter<any> = new EventEmitter<any>();
  @Output() public fileSelected: EventEmitter<any> = new EventEmitter<any>();
  @Output() public amountChange: EventEmitter<any> = new EventEmitter<any>();
  @Output() public downPaymentChange: EventEmitter<any> = new EventEmitter<any>();
  @ViewChild('productTypes', { static: false }) productTypesviewChild: PsDropdownProductTypesComponent;

  selectedFileData: any;
  readonlyMode: boolean;

  get defaultOptions(): IOptionsPsComplexFacilityRequestExposed {
    return this.options;
  }

  requestedProductPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'requested_products_key',
    iconName: 'document',
    expanded: true
  };

  constructor(
    commonService: PsCommonService,
    public omniPull: OmniPullService,
    logger: LoggerService
  ) {
    super(commonService, logger);
  }

  ngOnInit() {
    super.ngOnInit();
    let operCustomization = this.common.returnOperCustomization();
    this.readonlyMode = operCustomization.get(ConstantCommon.FORM_READONLY).IS_READONLY || false;
  }
  onFacilityTypeChanged(value: any) {

    if (value) {
      this.productTypesviewChild.loadProductTypes(value.selectedObj.omniFacilityTypeClassList,value.selectedObj.appType);
      //Michel Jira #5919 : Disable all the fields if in readmode
      if (!this.readonlyMode) {
        if (value.selectedObj.appType == CommonBussinessConstant.FACILITY_TYPE_SPECIFIC) {
          if (value.selectedObj.omniFacilityTypeClassList.length > 0) {
            this.commonProv.setValInsideNestedObj(this.options.productClassOptions.fcName, value.selectedObj.omniFacilityTypeClassList[0].productClassCode, this.options.requestObject);
          }
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.options.productClassOptions.fcName], 1);
        }
        else if (value.selectedObj.appType === CommonBussinessConstant.FACILITY_TYPE_GENERAL) {
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.options.productClassOptions.fcName], 1);
          this.commonProv.setValInsideNestedObj(this.options.productClassOptions.fcName, { itemValue: "" }, this.options.requestObject);
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.options.productClassOptions.fcName], 0);

        }
      }
    }
    this.facilityTypeChange.emit(value);

  }
  // Salah FCBDBI230071
  onProductClassChanged(value: any) {
    this.productClassChange.emit(value);
  }
  onAmountChanged(value: any) {
    if (value) {
      this.amountChange.emit(value);
    }
  }

  onDownPaymentChange(value: any) {
    if (value) {
      this.downPaymentChange.emit(value);
    }
  }
  selectFile(event) {
    if (event) {
      this.fileSelected.emit(this.selectedFileData);
    }
  }


}
